.search-bar {
    background-color: var(--theme_yellow);
    margin: 0px;
    height: 75%;
    width: 100%;
    display: flex;
    position: fixed;
    top: 0%;
    z-index: 99;
}

.search-bar-enter {
    background-color: var(--theme_yellow);
    margin: 0px;
    height: 75%;
    display: flex;
}

.search-bar-enter-active {
    margin: 0px;
    height: 10%;
    transition: height 1s;
}

.search-bar-enter-done {
    margin: 0px;
    height: 10%;
    transition: height 1s;
}

.search-bar-field {
    position: absolute;
    margin: 0% 10%;
    top: 20%;
    width: 80%;
}

.search-bar-field-enter {
    position: absolute;
    margin: auto 10%;
    top: 20%;
    width: 80%;
}

.search-bar-field-enter-active {
    top: 5px;
    transition: 1s;
}

.search-bar-field-enter-done {
    top: 5px;
    transition: 1s;
}

.search-bar h2 {
    margin: auto 0px;
}

#SearchField {
    margin: 0;
    height: 50px;
}

#SearchField input {
    width: 92%;
    height: 80%;
    box-sizing: border-box;
}

#SearchField button {
    width: 7%;
    height: 80%;
    box-sizing: border-box;
}